import moment from 'moment-timezone';

// 試算表ID
const spreadsheetId = '1UrrPrOOsEifsIRUX6y0OLxixlXRSOIOB0XQ4S_a5CSE';
// 工作表名稱
const tabName = 'form1';
// API 金鑰
const apiKey = 'AIzaSyA18KqIPkobvlQJXyBlxarTB7HG4FEYwVA';

// const defaultMoment = moment().tz('Asia/Taipei');

// 武將與武將碎片分配結果
export async function getMartialAuctionResult(start, end) {
  const url = `https://sheets.googleapis.com/v4/spreadsheets/${spreadsheetId}/values/${tabName}/?key=${apiKey}`;
  let json;
  if (start) {
    start = moment(start, 'YYYY-MM-DD HH:mm');
  }
  if (end) {
    end = moment(end, 'YYYY-MM-DD HH:mm');
  }
  try {
    json = await (await fetch(url)).json();
    json.values = json.values.filter((value) => {
      const t = formatTime(value[0]);
      if (!t || !t.isValid()) {
        return false;
      }
      value[0] = t.format('YYYY-MM-DD HH:mm:ss');
      if (t && start && end) {
        return start.isBefore(t) && end.isAfter(t);
      } else if (t && start && !end) {
        return start.isBefore(t);
      } else if (t && !start && end) {
        return end.isAfter(t);
      } else if (t && !start && !end) {
        return true;
      }
      return false;
    });
  } catch (error) {
    window.alert('目前無法取得軍團拍賣的武將分配結果');
    console.error(error);
    json = undefined;
    return json;
  }
  return json;
}

function formatTime(googleSheetTimestamp) {
  if (googleSheetTimestamp) {
    const replacement = googleSheetTimestamp
      .replaceAll('上午', 'AM')
      .replaceAll('下午', 'PM');
    return moment(replacement, 'YYYY-MM-DD A hh:mm:ss');
  }
  return undefined;
}
